export class Widget {
  _id: string;
  widgetType: string;
  pageId: string;
  constructor(_id, widgetType, pageId) {
    this._id = _id;
    this.widgetType = widgetType;
    this.pageId = pageId;
  }
}

export class Heading extends Widget {
  size: number;
  text: string;

  constructor(_id, pageId, size, text) {
    super(_id, 'HEADING', pageId);
    this.size = size;
    this.text = text;
  }
}

export class Image extends Widget {
  width: string;
  url: string;

  constructor(_id, pageId, width, url) {
    super(_id, 'IMAGE', pageId);
    this.width = width;
    this.url = url;
  }
}

export class YouTube extends Widget {
  width: string;
  url: string;

  constructor(_id, pageId, width, url) {
    super(_id, 'YOUTUBE', pageId);
    this.width = width;
    this.url = url;
  }
}
